//
// Slick
//
// Overwrite/Extend styles
// --------------------------------------------------

.slick-slider {
  .slick-slide {
    outline: none;
  }

  &.slick-dotted {
    margin-bottom: 3rem;

    .slick-dots {
      bottom: -2rem;
    }
  }

  &.slick-dotted.slick-dotted-inner {
    margin-bottom: 0;

    .slick-dots {
      bottom: .625rem;
    }
  }

  &.slick-dotted.slick-dotted-white {
    .slick-dots li button::before {
      color: $white;
    }
  }

  .slick-prev,
  .slick-next {
    width: 2.5rem;
    height: 3.75rem;
    text-align: center;
    background-color: rgba(0,0,0,.03);
    z-index: 2;

    &:hover {
      background-color: rgba(0,0,0,.15);
    }

    &::before {
      font-family: 'Font Awesome 6 Free', 'Font Awesome 6 Pro';
      font-weight: 600;
      font-size: 28px;
      line-height: 28px;
      color: $primary-dark;
    }
  }

  .slick-prev {
    left: 0;

    &::before {
      content: '\f104';
    }
  }

  .slick-next {
    right: 0;

    &::before {
      content: '\f105';
    }
  }

  &.slick-nav-white {
    .slick-prev,
    .slick-next {
      background-color: rgba(255,255,255,.5);

      &:hover {
        background-color: rgba(255,255,255,1);
      }

      &:before {
        color: $black;
      }
    }
  }

  &.slick-nav-black {
    .slick-prev,
    .slick-next {
      background-color: rgba(0,0,0,.25);

      &:hover {
        background-color: rgba(0,0,0,1);
      }

      &::before {
        color: $white;
      }
    }
  }

  &.slick-nav-hover {
    .slick-prev,
    .slick-next {
      opacity: 0;
      transition: opacity .25s ease-out;
    }

    &:hover {
      .slick-prev,
      .slick-next {
        opacity: 1;
      }
    }
  }
}

.slick-disabled {
  .fa-chevron-right, .fa-chevron-left {
    color: $gray-400;
    cursor: no-drop;
  }
}
