#canvas-holder {
    padding-top: 100%;
    position: relative;

}
#hue-saturation-picker {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
}

#hue-saturation-picker-circle {
    width: 12px;
    height: 12px;
    position: absolute;
    left: 0;
    top: 0;
    cursor: pointer;
    border-radius: 100%;
    border: 2px solid black;
}